import Vue from 'vue';
import VueRouter from 'vue-router';
import Stats from  '../views/Stats.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '/',
    component: Stats,
  },
  {
    path: '/:iso',
    component: Stats,
  },
];

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
});

export default router;
